


































import Vue from "vue";
import { saveOrUpdate, getResource } from "@/services/resource";

export default Vue.extend({
  name: "CreateOrEditResource",
  props: {
    id: {
      type: [String, Number],
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
    resourceCategories: {
      type: Array,
      default: ()=>{
          return []
      },
    },
  },
  data() {
    return {
      form: {
        code: "",
        name: "",
        categoryId:"",
        description: "",
      },
    };
  },

  created() {
    // 如果是编辑操作，则根据角色 ID 加载展示角色信息
    if (this.isEdit) {
      this.loadResource();
    }
  },

  methods: {
    async loadResource() {
      const { data } = await getResource(this.id);
      this.form = data.data;
    },

    async onSubmit() {
      await saveOrUpdate(this.form);
      this.$message.success("操作成功");
      this.$emit("success");
    },
  },
});
