


















































































































import Vue from "vue";
import { getResourcePages, deletResource } from "@/services/resource";
import { getResourceCategories } from "@/services/resource-category";
import { Form } from "element-ui";
import CreateOrEdit from "./CreateOrEdit.vue";

export default Vue.extend({
  name: "ResourceList",
  data() {
    return {
      resources: [], // 资源列表
      form: {
        name: "",
        url: "",
        current: 1, // 默认查询第1页数据
        size: 5, // 每页大小
        categoryId: null, // 资源分类
      },
      id:'',
      totalCount: 0,
      resourceCategories: [], // 资源分类列表
      isLoading: true, // 加载状态
      dialogVisible:false,
      isEdit:false
    };
  },
  components: {
    CreateOrEdit,
  },
  created() {
    this.loadResources();
    this.loadResourceCategories();
  },

  methods: {
    async loadResourceCategories() {
      const { data } = await getResourceCategories();
      this.resourceCategories = data.data;
    },

    async loadResources() {
      this.isLoading = true; // 展示加载中状态
      const { data } = await getResourcePages(this.form);
      this.resources = data.data.records;
      this.totalCount = data.data.total;
      this.isLoading = false; // 关闭加载中状态
    },

    onSubmit() {
      this.form.current = 1; // 筛选查询从第 1 页开始
      this.loadResources();
    },
     handleAdd () {
      this.isEdit = false
      this.dialogVisible = true
    },

    handleEdit (item: any) {
      this.dialogVisible = true
      this.id = item.id
      this.isEdit = true
    },

    async handleDelete(item: any) {
      console.log("handleDelete", item);
       try {
        await this.$confirm(`确认删除资源：${item.name}？`, '删除提示')
        await deletResource(item.id)
        this.$message.success('删除成功')
        this.loadResources()
      } catch (err) {
        if (err && err.response) {
          this.$message.error('删除失败，请重试')
        } else {
          this.$message.info('取消删除')
        }
      }
    },

    handleSizeChange(val: number) {
      this.form.size = val;
      this.form.current = 1; // 每页大小改变重新查询第1页数据
      this.loadResources();
    },

    handleCurrentChange(val: number) {
      // 请求获取对应页码的数据
      this.form.current = val; // 修改要查询的页码
      this.loadResources();
    },

    onReset() {
      (this.$refs.form as Form).resetFields();
      this.form.current = 1; // 重置回到第1页
      this.loadResources();
    },

     onSuccess () {
      this.dialogVisible = false // 关闭对话框
      this.loadResources() // 重新加载数据列表
    },
  },
});
